.footer__content {
  padding: 0 250px;
  padding-top: 100px;
  padding-bottom: 0;
}

.footer__top {
  display: flex;
  justify-content: space-between;
  padding-bottom: 95px;
}

.footer__column:nth-child(2) {
  margin-left: -60px;
}

.footer__item:not(:last-child) {
  margin-bottom: 17px;
}

.footer__list--row {
  display: flex;
  align-items: center;
}

.footer__list--row .footer__item {
  margin-bottom: 0;
}

.footer__list--row .footer__item:not(:last-child) {
  margin-right: 43px;
}

.footer__link {
  color: var(--color-tundora);
  font-size: 13px;
}

.footer__link--row {
  color: var(--color-dark);
  font-size: 13px;
  font-weight: 700;
}

.footer__title {
  margin: 0;
  margin-bottom: 35px;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

.footer__form {
  width: 646px;
  margin-bottom: 25px;
  transform: translateY(3px);
}

.footer__text {
  color: var(--color-tundora);
  font-size: 13px;
  font-weight: 500;
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #eeeeee;
  padding: 40px 0;
}

.footer__social {
  margin-right: 12px;
}

.subscribe-form {
  position: relative;
}

.subscribe-form__title {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 13px;
  color: var(--color-tundora);
}

.subscribe-form__field {
  display: block;
  width: 100%;
  padding-bottom: 10px;
  border-bottom: 2px solid var(--color-dark);
}

.subscribe-form__field input {
  width: 100%;
  padding-right: 80px;
  border: none;
  outline: none;
  font-size: 13px;
  font-weight: 500;
  color: var(--color-dark);
}

.subscribe-form__field input::placeholder {
  color: rgba(69, 69, 69, 0.5);
}

.subscribe-form__btn {
  position: absolute;
  z-index: 2;
  right: 0;
  bottom: 0;
  padding: 13px 10px;
  color: var(--color-dark);
  font-size: 13px;
  font-weight: 500;
}

.social {
  display: flex;
  align-items: center;
}

.social__item:not(:last-child) {
  margin-right: 29px;
}

.social__link {
  display: block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.social__link--tw {
  background-image: url("../../img/twitter.png");
}

.social__link--fb {
  background-image: url("../../img/facebook.png");
}

.social__link--inst {
  background-image: url("../../img/instagram.png");
}

.social__link--pt {
  background-image: url("../../img/pinterest.png");
}
